import helpers from '../../helpers'
import Graphic from './Graphic'
import BarText from './BarText'

export default class Bar extends Graphic {
    constructor(props) {
        super(props)

        this.animation = true
        this.animationDuration = 800
        this.animationDelay = props.animationDelay

        this.block = props.block
        this.state = {
            start: props.start,
            duration: props.end - props.start,
            color: props.color
        }
        this.text = props.text

        this.interactable = "true"
        this.hitRegion = {
            
        }
        this.children = []
        if (props.text.length > 0) {
            this.children = [
                new BarText({
                    ...props,
                    block: props.block
                })
            ]
        }
    }

    animateToWave = () => {
        this.children = []
    }

    render = (timeStamp) => {
        const { chart, ctx, progress } = this
        const { start, duration, color } = this.state

        const animatedEnd = start + progress(duration)

        ctx.fillStyle = color
        ctx.globalAlpha = progress(1)
        // if (start > 960) {
        //     ctx.globalAlpha = progress(1) * 0.2
        // }
        this.createSegment(-10, -100, start, animatedEnd, function () {
            ctx.fill()
        })

        this.renderChildren()
    }
}


