import Graphic from './Graphic'

export default class Number extends Graphic {
    constructor(props) {
        super(props)

        this.animation = true
        this.animationDuration = 1000
        this.animationDelay = props.animationDelay

        this.state = {
            dist: props.dist,
        }
    }

    render = (timeStamp) => {
        const { chart, ctx, helpers, progress, block } = this
        const { shape } = block


        const sunCentre = {
            x: (shape.elements[0].endPoint.x + shape.elements[0].startPoint.x) / 2,
            y: shape.elements[0].endPoint.y + 20
        }
        ctx.beginPath()
        ctx.strokeStyle = '#f4c242'
        ctx.lineWidth = 8

        const ourAngle = -Math.PI + this.state.dist
        // var f = this.minutesToXY(hour * 60, progress(50))
        ctx.moveTo(sunCentre.x + Math.cos(ourAngle) * 20, sunCentre.y + Math.sin(ourAngle) * 20)

        ctx.lineTo(sunCentre.x + Math.cos(ourAngle) * (20 + progress(60)),
            sunCentre.y + Math.sin(ourAngle) * (20 + progress(60))
        )
        ctx.stroke()
    }
}


