import Graphic from '../draw/components/Graphic'
import TimeZoneSignatureGMT from './TimeZoneSignatureGMT'

export default class TimeZoneSignature extends Graphic {
    constructor(props) {
        super(props)

        this.animation = true
        this.animationDuration = 2000

        this.p = props.p
        this.text = props.text
        this.city = props.city
        
        
        this.children = [
            new TimeZoneSignatureGMT({
                ...props,
                width: props.width,
                animationDelay: 1000
            })
        ]
    }

    changeShape = () => {

    }


    render = (timeStamp) => {
        const { chart, ctx, helpers, progress, progressColor, p, text, city } = this

        ctx.font = `100px Raleway`

        ctx.textAlign = 'left'
        ctx.textBaseline = 'middle'
        this.animateText(city, p.x, p.y)

        this.renderChildren()
    }
}


