import helpers from '../../helpers'
import Graphic from './Graphic'
import Hour from './Hour'

export default class Hours extends Graphic {
    constructor(props) {
        super(props)

        this.animation = true
        this.animationDuration = 2000

        this.block = props.block


        this.children = []

        for (let i = 0; i < 24; i++){
            this.children.push(new Hour({
                ...props,
                block: this.block,
                minutes: i * 60,
                animationDelay: 0 + i*150
            }))
        }
    }

    render = (timeStamp) => {
        this.renderChildren()
    }
}


