import helpers from '../../helpers'
import Graphic from './Graphic'


export default class BarText extends Graphic {
    constructor(props) {
        super(props)

        this.animation = true
        this.animationDuration = 1000
        this.animationDelay = props.animationDelay

        this.block = props.block
        this.state = {
            start: props.start,
            duration: props.end - props.start,
            color: props.color,
            text: props.text
        }

        this.interactable = "true"
        this.hitRegion = {

        }
    }

    render = (timeStamp) => {
        const { chart, ctx, progress } = this
        const { start, duration, color, text } = this.state

        const animatedEnd = start + progress(duration)

        ctx.fillStyle = "black"
        ctx.globalAlpha = progress(1)
        // if (start > 960) {
        //     ctx.globalAlpha = progress(1) * 0.2
        // }
        const pos = this.minutesToXY((start + animatedEnd) / 2, -135, true)
        ctx.font = `20px Raleway`
        ctx.fillText(text, pos.x, pos.y)
        // this.createSegment(-10, -100, start, animatedEnd, function () {
        //     ctx.fill()
        // })
    }
}


