
import BaseComponent from './BaseComponent'

export default class BoundsDebug extends BaseComponent {
    constructor(props) {
        super(props)

        this.animation = true

        this.bounds = props.bounds
        this.city = props.city
    }

    render = () => {
        const { ctx, bounds } = this
        ctx.save()
        ctx.globalAlpha = 0.05
        ctx.fillRect(bounds.xstart, bounds.ystart, bounds.xend - bounds.xstart, bounds.yend - bounds.ystart)
        ctx.restore()
        ctx.font = "30px verdana"
        ctx.fillText('city: ' + this.city, bounds.xstart, bounds.ystart)
    }
}


