import Graphic from '../draw/components/Graphic'

export default class TimeZoneSignatureGMT extends Graphic {
    constructor(props) {
        super(props)

        this.animation = true
        this.animationDuration = 2000
        this.animationDelay = props.animationDelay

        this.p = props.p
        this.text = props.text
        this.width = props.width

    }


    render = (timeStamp) => {
        const { chart, ctx, helpers, progress, progressColor, p, text, width } = this



        ctx.textAlign = 'left'
        ctx.textBaseline = 'middle'

        ctx.font = `40px Raleway`
        ctx.fillStyle = "rgba(0,0,0,0.5)"
        ctx.globalAlpha = progress(1)
        ctx.fillText(text, p.x + width + 25, p.y + 20)
    }
}


