import BaseComponent from '../draw/components/BaseComponent'
import Block from './Block'
import BoundsDebug from '../draw/components/BoundsDebug'
import ColorScheme from 'color-scheme'

export default class ChartSet extends BaseComponent {
    // top of hierarchy

    constructor(props) {
        super(props)

        this.bounds = props.bounds

        this.props = {
            city: props.city,
            elements: props.elements
        }

        this.children = [
            new Block({
                ...props,
                colors: props.chart.colors,
                elements: this.props.elements,
                bounds: this.bounds,
                date: props.date,
            }),
            // new Block({
            //     elements:
            // })
            // new BoundsDebug({
            //     ...props,
            //     bounds: this.bounds,
            // })
        ]
    }

    changeShape = () => {
        this.children[0].changeShape()
    }
    
    render = () => {
        this.children.forEach(child => child.render())
    }
}


