
import BaseComponent from './BaseComponent'
import helpers from '../../helpers'


export default class ShapeDebug extends BaseComponent {
    constructor(props) {
        super(props)

        this.animation = true
        this.animationDuration = 2000
        this.easing = 'linear'

        this.dotRadius = 10
        this.block = props.block
    }

    createDot = (x, y) => {
        const { ctx } = this
        ctx.beginPath();
        ctx.arc(x, y, this.dotRadius, 0, 2 * Math.PI);
        ctx.fill();
    }

    render = () => {
        const { chart, ctx, helpers, progress, block } = this
        const { shape } = block

        ctx.save()
        ctx.globalAlpha = progress(1)
        ctx.fillStyle = 'blue'
        shape.elements.forEach(element => {
            this.createDot(element.startPoint.x, element.startPoint.y)
        });

        ctx.fillStyle = 'red'
        shape.elements.forEach(element => {
            this.createDot(element.circleCenter.x, element.circleCenter.y)
        });

        ctx.fillStyle = 'green'
        shape.elements.forEach(element => {
            this.createDot(element.endPoint.x, element.endPoint.y)
        });

        ctx.strokeStyle = 'gray'
        ctx.setLineDash([3, 5])
        shape.elements.forEach(element => {
            this.drawLine(element.startPoint, element.endPoint);
            const distance = Math.round(helpers.distance(element.startPoint.x, element.startPoint.y, element.endPoint))
            ctx.fillText(`line: ${distance}`, (element.startPoint.x + element.endPoint.x) / 2, (element.startPoint.y + element.endPoint.y) / 2)
        });

        ctx.strokeStyle = 'gray'
        shape.elements.forEach(element => {
            const { circleCenter, circleRadius } = element
            ctx.beginPath();
            ctx.arc(circleCenter.x, circleCenter.y, circleRadius, 0, Math.PI * 2)
            ctx.stroke();

            const circumference = element.circleRadius * 2 * Math.PI
            const ourCirc = Math.round((element.totalAngle / (Math.PI * 2)) * circumference)
            ctx.fillText(`arc: ${ourCirc}`, (element.startPoint.x + element.endPoint.x) / 2, -100 + (element.startPoint.y + element.endPoint.y) / 2)
            
        });

        ctx.strokeStyle = 'indigo'
        shape.elements.forEach(element => {
            const { circleCenter, circleRadius, startPoint } = element

            const distanceCenterToLine = circleCenter.y - startPoint.y
            const relation = distanceCenterToLine / circleRadius
            ctx.fillText(`relation: ${relation}`, (element.startPoint.x + element.endPoint.x) / 2, -50 + (element.startPoint.y + element.endPoint.y) / 2)

        });

        ctx.strokeStyle = 'violet'
        shape.elements.forEach(element => {
            const { circleCenter, circleRadius, startPoint } = element
            const distance = Math.round(helpers.distance(element.startPoint.x, element.startPoint.y, element.endPoint))
            const circumference = element.circleRadius * 2 * Math.PI
            const ourCirc = Math.round((element.totalAngle / (Math.PI * 2)) * circumference)
            ctx.fillText(`relation: ${distance/ourCirc}`, (element.startPoint.x + element.endPoint.x) / 2, 50 + (element.startPoint.y + element.endPoint.y) / 2)

        });


        ctx.restore()
        ctx.save()
        ctx.lineWidth = chart.config.face.stroke
        ctx.strokeStyle = chart.config.face.strokeColor

        // ctx.beginPath()
        // helpers.createCurve(chart, 0, progress(1440), 0)
        // ctx.stroke()
        


        ctx.restore()

    }
}


