
import Graphic from '../draw/components/Graphic'
import BaseComponent from '../draw/components/BaseComponent'

import Bar from '../draw/components/Bar'
import ShapeDebug from '../draw/components/ShapeDebug'
import Line from '../draw/components/Line'
import View from '../draw/components/View'
import Sun from '../draw/components/Sun'
import Hours from '../draw/components/Hours'
import Number from '../draw/components/Number'
import NowMark from '../draw/components/NowMark'

import calculateShape from '../shape/calculateShape'

export default class Block extends Graphic {
    // top of hierarchy

    constructor(props) {
        console.log('â', props)
        super(props)
        this.props = props

        // this.children = 
        this.state = {
            shape: 'circle'
        }

        this.children = []



        this.block = {

        }

        this.view = new View({
            ...props
        })
        this.block.shape = this.view.getShape()
        console.log('calculated shape for ð', this.props.city, this.block.shape)

        this.animation = true;
        
        const colors = props.colors

        this.props.elements.forEach((element, i) => {
            this.children.push(new Bar({
                ...props,
                block: this.block,
                start: element.start,
                end: element.end,
                text: element.text || '',
                animationDelay: 400 + i * 300,
                color: `#${colors[Math.floor(Math.random() * colors.length)]}`
            }))
        });


        this.children.push(new Line({
            ...props,
            block: this.block,
        }))

        const n = ['8am', '12pm', '16pm', '20pm', '12am', '4am']
        for (let Ã¸ = 0; Ã¸ < 6; Ã¸++){

            this.children.push(new Number({
                ...props,
                block: this.block,
                hour: 2 + 4 * Ã¸,
                text: n[Ã¸],
                animationDelay: 80*Ã¸
            }))
        }

        // this.children.push(new Number({
        //     ...props,
        //     block: this.block,
        //     hour: -2 + 8,
        //     text: "12pm"
        // }))

        // this.children.push(new Number({
        //     ...props,
        //     block: this.block,
        //     hour: -2 + 12,
        //     text: "16pm"
        // }))

        // this.children.push(new Number({
        //     ...props,
        //     block: this.block,
        //     hour: -2 + 16,
        //     text: "20pm"
        // }))

        // this.children.push(new Number({
        //     ...props,
        //     block: this.block,
        //     hour: 18,
        //     text: "12am"
        // }))

        // this.children.push(new Number({
        //     ...props,
        //     block: this.block,
        //     hour: 22,
        //     text: "4am"
        // }))

        this.children.push(new Hours({
            ...props,
            block: this.block
        }))

        // this.children.push(new ShapeDebug({
        //     ...props,
        //     block: this.block,
        // }))


    }


    changeShape = () => {
        this.view.animateToWave()

        this.children.unshift(new Sun({
            ...this.props,
            block: this.block,
            date: this.props.date
        }))

        this.children.filter(c => {
            console.log(c.text)
            return ["12am", "4am"].includes(c.text)
        }).forEach(c => c.animateToWave())
    }
    
    render = () => {
        const { progress } = this
        this.block.shape = this.view.getShape()
        
        this.renderChildren()
    }
}


