import helpers from '../../helpers'
import Graphic from './Graphic'


export default class Line extends Graphic {
    constructor(props) {
        super(props)

        this.animation = true
        this.animationDuration = 2000
        this.easing = 'linear'

        this.block = props.block
    }

    render = (timeStamp) => {
        const { ctx, progress } = this

        ctx.beginPath()
        this.createCurve(0, progress(1440), 0)
        ctx.stroke()
    }
}


