import Graphic from './Graphic'
import moment from 'moment'

export default class SunDate extends Graphic {
    constructor(props) {
        super(props)

        this.props = props
        this.animation = true
        this.animationDuration = 1000
        this.animationDelay = props.animationDelay

    }

    render = (timeStamp) => {
        const { chart, ctx, helpers, progress, block } = this
        const { shape } = block


        const sunCentre = {
            x: (shape.elements[0].endPoint.x + shape.elements[0].startPoint.x) / 2,
            y: shape.elements[0].endPoint.y + 20
        }
        ctx.font = `28px Raleway`
        
        ctx.fillStyle = '#5876b0'
        this.animateText(this.props.date.format('dddd MMMM Do'), sunCentre.x, sunCentre.y + 40)
    }
}


