import helpers from '../../helpers'

export default class BaseComponent {
    constructor(props) {
        this.initTime = performance.now()
        this.chart = props.chart
        this.ctx = props.chart.ctx
        this.helpers = helpers
        // animation defaults
        this.animation = false
        this.animationDuration = 10000
        this.easing = 'easeInOutCubic'
        this.animationDelay = props.animationDelay || 0
    }

    renderChildren = () => {
        this.children.forEach(child => child.requestRender())
    }

    requestRender = () => {
        // calculate animations et c
        const timeStamp = performance.now()
        if (this.animation) {
            const animationProgress = Math.max(Math.min(((timeStamp - this.initTime) - this.animationDelay) / this.animationDuration, 1), 0)
            const withEasing = helpers.easingEffects[this.easing](animationProgress)
            
            this.animationProgress = withEasing
        }

        this.ctx.save()

        this.render(timeStamp)

        this.ctx.restore()
    }

    render = () => {
        throw new Error(
            `You have to define your own render function you ðº`
        );
    }

    progress = v => {
        if (!this.chart.animation) {
            return v
        }
        return this.animationProgress * v
    }

    progressColor = (from, to) => {
        if (!this.chart.animation) {
            return to
        }
        const anim = from.map((f, i) => {
            const range = to[i] - f
            return f + this.progress(range)
        })

        return `rgba(${anim[0]},${anim[1]},${anim[2]},${anim[3]})`
    }

    drawLine = (startPoint, endPoint) => {
        const { ctx, progress } = this

        const endPointAnimated = {
            x: startPoint.x + progress(endPoint.x - startPoint.x),
            y: startPoint.y + progress(endPoint.y - startPoint.y)
        }
        ctx.moveTo(startPoint.x, startPoint.y);
        ctx.lineTo(endPointAnimated.x, endPointAnimated.y);
        ctx.stroke();
    }

    animateText = (text, x, y) => {
        const { ctx, progress } = this
    
        const letterProgress = Math.round(progress(text.length))
        ctx.fillText(text.substring(0, letterProgress), x, y)
    }
}


