import helpers from '../../helpers'
import Graphic from './Graphic'


export default class Hours extends Graphic {
    constructor(props) {
        super(props)

        this.animation = true
        this.animationDuration = 500
        this.animationDelay = props.animationDelay
        this.block = props.block

        this.state = {
            minutes:props.minutes
        }
    }

    render = (timeStamp) => {
        const { chart, ctx, helpers, progress } = this
        const { minutes } = this.state

        ctx.globalAlpha = progress(1)
        var p = this.minutesToXY(minutes, 0)
        var pu = this.minutesToXY(minutes, progress(30))

        ctx.strokeStyle = '#D3D3D3'

        ctx.moveTo(p.x, p.y)
        ctx.lineTo(pu.x, pu.y)
        ctx.stroke()
    }
}


