import Graphic from './Graphic'
import SunStrokes from './SunStrokes'
import SunDate from './SunDate'

export default class Number extends Graphic {
    constructor(props) {
        super(props)

        this.animation = true
        this.animationDuration = 2000
        this.animationDelay = 500

        this.state = {
            hour: props.hour,
            text: props.text,
        }

        this.children = [
            
        ]

        for (let i = 0; i < 6; i++){
            this.children.push(new SunStrokes({
                ...props,
                block: props.block,
                dist: (i + 0.5) * Math.PI / 6,
                animationDelay: 1000 + i*50
            }))
        }
        console.log('props.date', props.date)
        this.children.push(new SunDate({
            ...props,
            block: props.block,
            animationDelay: 2000,
            date: props.date
        }))
    }

    render = (timeStamp) => {
        const { chart, ctx, helpers, progress, block } = this
        const { shape } = block


        console.log()

        const sunCentre = {
            x: (shape.elements[0].endPoint.x + shape.elements[0].startPoint.x) / 2,
            y: shape.elements[0].endPoint.y + 20
        }
        ctx.save()
        ctx.strokeStyle = '#f4c242'
        ctx.lineWidth = progress(40)
        if (progress(20) === 0) {
            return
        }

        // var f = this.minutesToXY(hour * 60, progress(50))
        ctx.beginPath()
        ctx.arc(sunCentre.x, sunCentre.y, 20, -Math.PI, 0)
        
        ctx.stroke()


        ctx.beginPath()
        ctx.lineWidth = progress(8)
        ctx.globalAlpha = 0.3
        this.createCurve(120, 120 + progress(720), 0)
        ctx.stroke()

        ctx.restore()
        ctx.lineWidth = 1
        ctx.strokeStyle = '#565656'
        ctx.beginPath()
        
        ctx.globalAlpha = 0.1 + progress(0.6)
        // ctx.moveTo(shape.elements[0].startPoint.x + 140, shape.elements[0].startPoint.y)
        // ctx.lineTo(shape.elements[0].endPoint.x - 140, shape.elements[0].endPoint.y)
        // ctx.stroke()

        this.drawLine({
            y: shape.elements[0].startPoint.y + 20,
            x: shape.elements[0].startPoint.x + 140
        }, {
                y: shape.elements[0].startPoint.y + 20,
                x: shape.elements[0].endPoint.x - 140
            })


        this.renderChildren()
    }
}


