import Graphic from './Graphic'

export default class Number extends Graphic {
    constructor(props) {
        super(props)

        this.animation = true
        this.animationDuration = 2000
        this.animationDelay = props.animationDelay

        this.state = {
            hour: props.hour,
            text: props.text,
        }

        this.text = props.text
        this.fillStyleFrom = [43,43,43, 0.5]
        this.fillStyle = [43, 43, 43, 1]
        this.flag = false
    }

    animateToWave = () => {
        this.fillStyleFrom = [43, 43, 43, 1]
        this.fillStyle = [43, 43, 43, 0.5]
        // this.
        this.flag = true
        this.initTime = performance.now()
    }

    render = (timeStamp) => {
        const { chart, ctx, helpers, progress, progressColor } = this
        const { hour, text } = this.state

        let fontSize;
        if (!this.flag) {
            fontSize = 10 + progress(30)
        } else {
            fontSize = 10 + 30
        }
        // ctx.font = `thin ${fontSize}px system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif`
        ctx.font = `${fontSize}px Raleway`
        
        if (!this.flag) {
            var p = this.minutesToXY(hour * 60, 20 + progress(50))
        } else {
            var p = this.minutesToXY(hour * 60, 20 + 50)
        }
        ctx.fillStyle = progressColor(this.fillStyleFrom, this.fillStyle)
        if (this.flag) {
            console.log(progressColor(this.fillStyleFrom, this.fillStyle))
        }
            
        ctx.fillText(text, p.x, p.y)

        ctx.strokeStyle = 'rgb(43,43,43)'

        var f = this.minutesToXY(hour * 60, progress(50))
        
        ctx.moveTo(f.x, f.y +10)
        ctx.lineTo(f.x, f.y +10 + progress(40))
        ctx.stroke()
    }
}


