
import BaseComponent from './BaseComponent'

export default class NowMark extends BaseComponent {
    constructor(chart, props) {
        super(chart, props)

        this.animation = true
        this.animationDuration = 1000
        this.animationDelay = 2000
        this.dotRadius = 10
    }

    render = () => {
        const { chart, ctx, helpers, progress } = this
        
        const date = new Date();
        const nowMinutes = 500//date.getHours() * 60 + date.getMinutes()
        var p = helpers.minutesToXY(chart, nowMinutes, 0)
        var e = helpers.minutesToXY(chart, nowMinutes, progress(700))

        // var e1 = helpers.minutesToXY(chart, nowMinutes - 10, progress(700) + 3)
        // var e2 = helpers.minutesToXY(chart, nowMinutes + 10, progress(700) + 3)



        ctx.moveTo(p.x, p.y)
        ctx.lineTo(e.x, e.y)

        ctx.stroke()        


        // ctx.moveTo(e1.x, e1.y)
        // ctx.lineTo(e2.x, e2.y)
        
        // ctx.stroke()

        ctx.beginPath();
        ctx.arc(e.x, e.y, this.dotRadius, 0, progress(2 * Math.PI));
        ctx.stroke();

    }
}


